/* eslint-disable */
import React from 'react'
import Bloc from './Components/Bloc';
import TextareaAutosize from 'react-textarea-autosize';

class Main extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      title: '',
      ref: '',
      contente:'',
      body: [
        {
        }
      ]
    };
    this.shortcut = {
      shorts: [
        {
          brut: "ajd",
          replaced: "aujourd'hui"
        },
        {
          brut: "slt",
          replaced: "salut"
        },
        {
          brut: "/lorem ",
          replaced: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec convallis mollis molestie. Nunc volutpat et metus nec convallis. Phasellus velit felis, maximus sit amet faucibus sed, molestie et metus. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus et ante sed nisl vestibulum posuere. Pellentesque urna tellus, ornare dapibus vehicula eu, dapibus id arcu. Donec venenatis eget lacus at blandit."
        }
        ,
        {
          brut: "/raphouel ",
          replaced: "On veut pas baiser les gos les plus putes, mais boire les eaux les plus pures"
        },
        {
          brut: "quoi",
          replaced: "feur"
        }
      ]
    }
  }

  componentDidMount() {
    this.updateNoteData(this.props.activeNote);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.activeNote !== this.props.activeNote) {
      this.updateNoteData(this.props.activeNote);
      console.log(this.props.activeNote);
    }
  }

  updateTitle = (evt) => {
    const val = evt.target.value;
    if (val) {
        this.setState({title: val});
        this.props.activeNote.title = val;
    }
  }

  updateNoteData = (activeNote) => {
    this.setState({
        id: activeNote.id,
        title: activeNote.title,
        body: activeNote.body,
        contente: activeNote.contente
    });

}

  handleTitleChange = (event) => {
    this.setState({ title: event.target.value });
  }

  handleListener = (index) => {
    let val = this.state.body[index].content;


    this.shortcut.shorts.map((el) => {
      console.log(el.brut + " -> " + el.replaced);
      if(val.includes(el.brut)) {
        let replaced = this.state.body[index].content.replace(el.brut, el.replaced);
        this.state.body[index].content = replaced;
      }
    })
    /*
    if (val.includes("ajd")) {
      let replaced = this.state.body[index].content.replace("ajd", "aujourd'hui");
      this.state.body[index].content = replaced;
    }
    */
  }

  handleBodyChange = (event, index) => {

    let newBody = this.state.body;
    newBody[index].content = event.target.value;
    this.setState({body: newBody});
    this.props.activeNote.body[index].content = event.target.value

    this.handleListener(index);
  }

  handleDeleteBlock = (current) => {
    let oldBody = this.state.body;
    console.log(oldBody);
    const removed = oldBody.splice(current, 1);
    console.log(removed);

    this.setState({body: oldBody});
    this.props.activeNote.body = oldBody;


  }

  handleAddBlock = (current) => {
    let actual = current + 1;
    this.setState({ref: actual});

    console.log(current);
    console.log(this.state.body);


    let newBody = this.state.body;
    let avaibleIndex = this.state.body.length;
    let newBlock = {
        id: avaibleIndex,
        content: '',
        style: {
          
        }
    }

    newBody.splice(current + 1, 0, newBlock);

    this.setState({body: newBody});
    this.props.activeNote.body = newBody;

  }

  render() {


    return (
        <div className="app-body">
            <div className="app-body-title">
                <TextareaAutosize
                    id="title"
                    placeholder="Title"
                    value={this.state.title}
                    onChange={this.handleTitleChange}
                    onKeyUp={evt => { this.updateTitle(evt);
                        this.props.setActiveNote(this.state.id);
                    }}/>
            </div>
            <div className="app-body-markdown">
                {this.props.activeNote.body.map((blc, index) => {
                    console.log("LE " + this.state.ref)
                    //return <textarea value={this.state.body[blc.id].content} onChange={ev => {this.handleBodyChange(ev, blc.id)}}></textarea>
                    if (index === this.state.ref) {
                        console.log(blc);
                        let compt = (
                            <Bloc
                              keyed={this.handleAddBlock}
                              id={index}
                              onClick={"l'index est : " +console.log(index)}
                              val={blc.content}
                              change={ev => {this.handleBodyChange(ev, index)}}
                              ref={this.comptRef}
                              autof={true}
                              delet={this.handleDeleteBlock}
                              styles={blc.style}
                            />
                          );
                        return compt;
                    } else {
                      console.log(blc);
                        let compt = (
                            <Bloc
                              keyed={this.handleAddBlock}
                              id={index}
                              val={blc.content}
                              change={ev => {this.handleBodyChange(ev, index)}}
                              ref={this.comptRef}
                              autof={false}
                              delet={this.handleDeleteBlock}
                              styles={blc.style}
                            />
                          ); 
                        return compt;
                    }
                })}
            </div>
        </div>
    );
}
}

export default Main;
